import React from 'react'
import Input from './Input'
import '../index.css'

const Contact = () => {
    return (
        // Component
        <div id="contact" className="bg-ntps-white mt-6 pb-12">
            
            { /* Container */ }
            <div className="mt-6 pt-10 container">

                { /* Flexbox */ }
                <div className="mt-6 flex justify-between">

                    { /* Paragraph text */ }
                    <div className="w-full">
                        <h2 className="text-3xl md:text-4 lg:text-5xl text-left text-ntps-blue mb-6">
                            Contact
                        </h2>

                        <div className="flex gap-6">
                            <Input placeholder="Naam" width="w-4/12" />
                            <Input placeholder="Email" width="w-4/12" />
                            <Input placeholder="Telefoon" width="w-4/12" />
                        </div>

                        <textarea
                            type="text"
                            placeholder="Omschrijving project"
                            className={`block align-text-top w-full h-96 mt-4 mb-2 bg-white p-2 rounded-lg border-2 border-white shadow-md focus:outline-none focus:border-indigo-500`}
                        ></textarea>

                        <button className="w-full h-12 rounded-md mt-4 bg-ntps-primary text-white hover:bg-white hover:text-ntps-primary border-2 border-ntps-primary transition duration-500">
                            Verstuur
                        </button>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Contact
